import React from 'react';
import ReactDOM from 'react-dom';
import Puzzle from 'react-image-puzzle';
import './index.css';

    
class Game extends React.Component
{

  basePuzzles = ["mercury", "venus", "earth", "mars", "jupiter", "saturn", "neptune", "uranus"]
  //basePuzzles = ["mercury","venus"]

  constructor(props)
  {
    super(props);

    var puzzles = this.basePuzzles.slice()
    var randomIndex = Math.floor(Math.random() * puzzles.length)
    var currentPuzzle = puzzles[randomIndex]
    puzzles.splice(randomIndex,1)

    this.state = {
      currentPuzzle : currentPuzzle,
      puzzles : puzzles,
      finished : false
    }

    this.restart = this.restart.bind(this);
  }

  restart()
  {
    var puzzles = this.state.puzzles.slice()
    if (puzzles.length > 0)
    {
       var randomIndex = Math.floor(Math.random() * puzzles.length)
       var currentPuzzle = puzzles[randomIndex]
       puzzles.splice(randomIndex)

       this.setState({
        currentPuzzle : currentPuzzle,
        puzzles : puzzles
      })
    }
    else
    {
      this.setState({
        finished : true
      })
    }
    
  }

  render()
  {
    var disabled = this.state.finished

    if (disabled)
    {
      return(<div>Finished</div>)
    }
    else
    return(
      <div>
        <Puzzle
        image={require('./' + this.state.currentPuzzle + ".jpg")}
        level='4' 
        onDone = {this.restart}
        />
        <br/>
        <br/>

        <br/>
      </div>
    )
//        <button onClick={this.restart} disabled={disabled}>Random Puzzle {this.state.puzzles.length}</button>        
  }
}

ReactDOM.render(
  <Game/>,
  document.getElementById('root')
);
